// Font Sizes
.fs12 {
    font-size: rem(12px);
}

.fs14 {
    font-size: rem(14px);
}

.fs18 {
    font-size: rem(18px);
}

.fs20 {
    font-size: rem(20px);
}

.fs24 {
    font-size: rem(24px);
}

.fs28 {
    font-size: rem(28px);
}

.fs30 {
    font-size: rem(30px);
}

html {
    color: rgb(41, 41, 41);
}

.center {
    text-align: center;
}

// flex helpers

.flex {

    display: flex;

    >*.full {
        flex: 1;
    }

    &.column {
        flex-direction: column;
    }

    &.justify-center {
        justify-content: center;
    }

    &.align-center {
        align-items: center;
    }

    &.space-between {
        justify-content: space-between;
    }

    &.space-around {
        justify-content: space-around;
    }
}

/* list helpers */

.clean-list {
    list-style-type: none;
    margin         : 0;
    padding        : 0;
}

// Media helpers

.square-ratio {
    @include aspect-ratio(1, 1);
}

.video-ratio {
    @include aspect-ratio(16, 9);
}

.portrait-ratio {
    @include aspect-ratio(9, 16);
}

// Display helpers 

.container {
    width : 1100px;
    margin: 0 auto;
}

.main-container {
    display        : flex;
    flex-direction : column;
    align-items    : center;
    justify-content: center;
    width          : 100vw;

}

//font awsome

i {
    cursor     : pointer;
    font-family: inherit;
}

.fas {
    font-family: fa-solid;
}

.fa {
    font-family: fa;
}

.relative {
    position: relative;
}

.absolute {
    position: absolute;
}

.sticky {
    position: sticky;
}

.fixed {
    position: fixed;
}

@media (max-width: $mobile-breakpoint) {
    .container {
        padding: 0 10px;
    }

}

@media (max-width: $normal-breakpoint) {}